<template>
  <div class="myDoll">
    <div class="user-header">
      <a class="nav-item1" @click="myDollBack"><img class="game_1" src="../../assets/images/usercenter/game_1.png"/></a>
      <a class="wode">我的娃娃</a>
      <a class="history" @click="historicalRecord">历史</a>
    </div>
    <div class="mydollBody" v-show="mydollBodyShow">
      <div class="myDollMain">
        <div class="myDollList">
          <ul id="list">
            <li v-for="site in recoderList">
              <input type="checkbox" :value="site.id" v-model="selectArr"/>
              <div class="wawaInfo">
                <img class="wawaImg" :src="site.icon_url"/>
                <div>
                  <p class="toptime"><a>{{site.name}}</a><span>{{site.created_at}}</span></p>
                  <p class="textovflow">{{site.remark}}</p>
                </div>
              </div>
              <!--<img class="playHishory" src="../../assets/images/thebar/wh_46.png"/>-->
              <a></a>
            </li>

          </ul>
        </div>
      </div>
      <div class="myDollFoot">
        <div class="Future">
          <p> <input type="checkbox" @click="selectAll" /><a>全选</a></p>
          <a>满3只包邮</a>
        </div>
        <div class="sureMail" @click="goToMail">
          邮寄
        </div>
      </div>
    </div>
    <div class="nomail" v-show="nomailShow">
      <a>~\(*>﹏<*)/~暂无娃娃!</a>
      <img src="../../assets/images/usercenter/wh_49none.png"/>
    </div>
  </div>
</template>
<script>
  function getCookie(name) {
    var v = window.document.cookie.match('(^|;) ?' + name + '=([^;]*)(;|$)');
    return v ? v[2] : null;
  }
  function setCookie(name, value, days) {
    var d = new Date;
    d.setTime(d.getTime() + 24*60*60*1000*days);
    window.document.cookie = name + "=" + value + ";path=/;expires=" + d.toGMTString();
  }
  var urlhost = process.env.API_AJAX;
  export default {
    data() {
      return {
        recoderList:[],
        mydollBodyShow:true,
        nomailShow:false,
        selectArr: []
      }
    },
    components: {
    },
    mounted:function () {
      var format = function (timestamp) {
        var date = new Date(timestamp * 1000);//时间戳为10位需*1000，时间戳为13位的话不需乘1000
        var Y = date.getFullYear() + '/';
        var M = (date.getMonth()+1 < 10 ? '0'+(date.getMonth()+1) : date.getMonth()+1) + '/';
        var D = date.getDate() + ' ';
        var h = date.getHours() + ':';
        var m = (date.getMinutes() < 10 ? "0" + (date.getMinutes()) : date.getMinutes());
        return Y+M+D+h+m;
      }
      var _this=this;
      var userId = getCookie('userId');
      $.ajax({
        type: 'GET',
        url: urlhost+"usercenter/packs?user_id="+userId,
        data$Type: "jsonp",
        data: {
        },
        success: function (response) {
          var mess = JSON.parse(response);
          console.log(mess)
          var dollList = mess.message;
          if(dollList.length >= 1){
          for(var i=0;i< dollList.length;i++){
            var num = parseInt(dollList[i].created_at)
            var time = format(num);
            dollList[i].created_at = time;
          }
          _this.recoderList = dollList;
        }else{
          _this.mydollBodyShow = false;
            _this.nomailShow = true;
        }
        },
      });

    },
    methods:{
      myDollBack:function () {
        this.$router.go(-1);
      },
      historicalRecord:function () {
        this.$router.push({path:"/usercenter/history"})
      },
      goToMail:function () {
          var _this = this;
          if(_this.selectArr.length == 0){
              _this.$layer.msg("邮寄的娃娃不能少于1个!")
          }else{
            _this.$router.push({
              path:"/usercenter/mail",
              query: {
                id: _this.selectArr
              }
            })
          }
      },
      selectAll(event) {
        var _this = this;
        if (!event.currentTarget.checked) {
          _this.selectArr = [];
        } else { //实现全选
          _this.selectArr = [];
          _this.recoderList.forEach(function(item) {
            _this.selectArr.push(item.id);
          });
        }
      }
    },
    watch: {
      selectArr(val) {
        var _this = this;
        _this.selectArr = val;
      }
    }
  }
</script>
<style lang="scss" scoped>
  @function n($n) {
    @return $n  / 64 + rem;
  };
  /*我的娃娃*/
  .myDoll{
    position: fixed;
    width: 100%;
    height: 100%;
    overflow-y: auto;
    top: 0px;
    background-color: #f2f2f2;
  .user-header{
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: n(80);
    background: #fff001;
    display: flex;
    justify-content: space-between;
    align-items: center;
    z-index: 999;
    border-bottom: 1px solid #d8d8d8;
  .nav-item1{
  .game_1{
    margin-left: n(23);
    width: n(50);
    height: n(50);
  }
  }
  .wode{
    font-size: 14px;
    font-weight: bold;
  }
  .history{
    margin-right: n(23);

  }
  }
  .myDollMain{
    width: 95%;
    margin-top: n(80);
    margin-left: 2.5%;
  .myDollList{
    margin-top: 10px;
    width: 100%;

  ul{
  li{
    display: inline-flex;
    justify-content: space-between;
    align-items: center;
    width: 100%;
    height: n(126);
    background: #ffffff;
    padding: 8px;
    -webkit-border-radius: 10px;
    -moz-border-radius: 10px;
    border-radius: 10px;
    margin-bottom: 10px;
  .wawaInfo{
    display: inline-flex;
  .wawaImg{
    width: n(100);
    height: n(100);
  }
  div{
    margin-left: 5px;
    width: n(380);
    height: n(100);
    display: inline-flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;

  .toptime{
    width: n(380);
    height: n(50);
    display: inline-flex;
    justify-content: space-between;
    align-items: center;

  }
  .textovflow{
    width: n(380);
    height: n(50);
    overflow:hidden;/*超出长度的文字隐藏*/
    text-overflow:ellipsis;/*文字隐藏以后添加省略号*/
    white-space:nowrap;/*强制不换行*/
  }
  }
  }
  .playHishory{
    width: n(60);
    height: n(60);
  }
  }
  }
  }

  }
  .myDollFoot{
    position: fixed;
    bottom: 0;
    width: 100%;
    height: n(77);
    background: #ffffff;
    display: inline-flex;
    z-index: 99999;
  .Future{
    width: 70%;
    height: 100%;
    display: inline-flex;
    align-items: center;
    justify-content: space-around;
  p{
    height: 100%;
    display: inline-flex;
    align-items: center;
  input{
    margin-right: 10px;
  }
  }
  }
  .sureMail{
    width: 30%;
    display: inline-flex;
    align-items: center;
    justify-content: center;
    background: #fef102;

  }
  }
    .nomail{
      margin-top: n(80);
      width: 100%;
      height: n(400);
      display: inline-flex;
      align-items: center;
      flex-direction: column;
      justify-content: center;
      img{
        margin-top: 10px;
        width: n(349);
        height: n(234);
      }
    }
  }
</style>




